import * as React from "react"
import { graphql } from 'gatsby'
import Img from 'gatsby-image'
import Layout from '../components/layout'
import downloadFile from '../downloads/file.pdf'
import Button from 'react-bootstrap/Button'

const ResumePage = ({ data }) => {
  return (
    <Layout pageTitle="Resume | ">
      <div style={{ textAlign: 'center' }}>
        <Img fixed={data.resumeImage.childImageSharp.fixed} />
        <div style={{ marginTop: '1rem' }}>
          <a href={downloadFile} download="resume" style={{ fontDecoration: 'none' }}>
            <Button variant="warning">
              <strong>Download</strong>
            </Button>{' '}
          </a>
        </div>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    resumeImage: file(relativePath: { eq: "resume.jpg" }) {
      childImageSharp {
        fixed(width: 612, height: 792, quality: 100) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`

export default ResumePage
